import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import Link from "../components/helpers/link"


export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        githubToml {
          organizations {
            name
            url
          }
        }
      }
    `
  )

  const githubData = data.githubToml.organizations
  const githubEntries = []

  for (const [index, org] of githubData.entries()) {
    githubEntries.push(
      <div className="nav-item col-12 col-md-6 p-0" key={"github-org-"+index}>
        <Link to={ org.url } className="nav-link p-0 mb-2">{ org.name }</Link>
      </div>
    )
  }

  return (
  <footer className="pb-5 bg-primary-3 text-light">
    <div className="container">
      <div className="row justify-content-center">
        <div className="col text-center"> 
          <Link to="https://salesforce.com">
            <img src="/assets/img/cloud-salesforce.svg" alt="Salesforce" className="mb-4" style={{height: 100}} />
          </Link>
        </div>
      </div>
      <div className="row mb-6 justify-content-center">
        <div className="col-md-10 col-lg-5 mb-3 text-lg-left text-md-center">
          <h5>About Salesforce</h5>
          <p className="pr-xl-3 text-light">
            Salesforce is the leader in customer relationship management (CRM), bringing companies closer to their customers. Salesforce enables companies of every size and industry to take advantage of powerful technologiesâ€”cloud, mobile, social, internet of things, and artificial intelligenceâ€”to connect to their customers. <Link className="hover-arrow">Go to salesforce.com</Link>
          </p>
        </div>
        <div className="col-md-3 col-lg-2 mb-3">
          <h5>Open Source</h5>
          <div className="d-flex flex-column">
            <div className="nav-item">
              <Link to="https://engineering.salesforce.com/" className="nav-link p-0 mb-2">Blog</Link>
            </div>
            <div className="nav-item">
              <Link to="https://twitter.com/salesforceeng" className="nav-link p-0 mb-2">Twitter</Link>
            </div>
            <div className="nav-item">
              <Link to="https://www.salesforce.com/company/careers/tech-product/" className="nav-link p-0 mb-2">Careers</Link>
            </div>
          </div>
        </div>
        <div className="col-md-6 col-lg-4">
          <h5>On GitHub</h5>
          <div className="d-flex flex-wrap">
            { githubEntries }
          </div>
        </div>
      </div>
      <div className="row justify-content-center text-center">
        <div className="col-xl-10">
          <ul className="list-inline mb-0">
            <li className="list-inline-item pr-2 mb-0 text-muted small">
              Copyright &copy; {(new Date().getFullYear())}, Salesforce, Inc
            </li>
            <li className="list-inline-item pr-2 mb-0 text-muted small">
              <Link to="https://www.salesforce.com/company/privacy/">
                Privacy Policy
              </Link>
            </li>
            <li className="list-inline-item pr-2 mb-0 text-muted small">
              <Link to="https://www.salesforce.com/company/legal/sfdc-website-terms-of-service.jsp">
                Terms
              </Link>
            </li>
            <li className="list-inline-item pr-2 mb-0 text-muted small">
              <Link to="#">
                Cookies
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </footer>
)}